import {ElMessage, ElNotification, ElLoading , ElMessageBox} from 'element-plus'


/**
 * 
 * @param {string} type  success/warning/info/error
 * @param {string} msg 
 * @param {boolean} center 
 * @param {boolean} showClose 
 * @param {number} duration , 1500 0 
 */
function messagePrompt(type = 'info', msg = '', center = true, showClose = false, duration = 3000) {
  ElMessage({
    type: type,
    message: msg,
    center: center,
    showClose: showClose,
    duration: duration
  });
}

/**
 * 
 * @param {string} title 
 * @param {string} msg 
 * @param {string} type  success/warning/info/error
 * @param {number} offset 
 * @param {string} position  top-right/top-left/bottom-right/bottom-left
 * @param {number} duration  3000
 * @param {boolean} showClose 
 */
function notifyPrompt(title = '', msg = '', type = 'info', offset = 0, position = 'top-right', duration = 3000, showClose = true) {
  ElNotification({
    title: title,
    message: msg,
    type: type,
    offset: offset,
    position: position,
    duration: duration,
    showClose: showClose
  });
}

/**
 * 
 * @param {string} text 
 * @param {boolean} lock 
 * @param {string} spinner 
 * @param {string} background 
 * @param {boolean} fullscreen 
 */
function loadingPrompt(text = '', lock = true, spinner = 'el-icon-loading', background = 'rgba(0, 0, 0, 0.7)', fullscreen = true) {
  let loadingInstance = ElLoading .service({
    text: text,
    lock: lock,
    spinner: spinner,
    background: background,
    fullscreen: fullscreen
  });
  return loadingInstance;
}

/**
 * 
 * @param content 
 * @param title 
 * @param confirmButtonText 
 * @param cancelButtonText 
 * @param confirmCallBack 
 * @param cancelCallBack 
 */
function messageBoxConfirm(content = '', title = '',confirmButtonText = '', cancelButtonText = '', confirmCallBack, cancelCallBack) {
  ElMessageBox.confirm(content, title, {
        confirmButtonText: confirmButtonText,
        cancelButtonText: cancelButtonText,
        type: 'warning'
    }).then(function (res) {
        if(confirmCallBack){
          confirmCallBack(res);
        }
    }).catch(function (err) {
        if(cancelCallBack){
            cancelCallBack(err);
        }
    });
}

/**
 * 
 * @param frameDiv
 * @returns {tooltip}
 */
function createTooltip(frameDiv) {
  let tooltip = function (frameDiv) {
    let div = document.createElement('DIV');
    div.className = "twipsy right";
    let arrow = document.createElement('DIV');
    arrow.className = "twipsy-arrow";
    div.appendChild(arrow);
    let title = document.createElement('DIV');
    title.className = "twipsy-inner";
    div.appendChild(title);
    this._div = div;
    this._title = title;
    this.message = '';
    // add to frame div and display coordinates
    frameDiv.appendChild(div);
    let that = this;
    div.onmousemove = function (evt) {
      that.showAt({x: evt.clientX, y: evt.clientY}, that.message);
    };
  };

  tooltip.prototype.setVisible = function (visible) {
    this._div.style.display = visible ? 'block' : 'none';
  };

  tooltip.prototype.showAt = function (position, message) {
    if (position && message) {
      this.setVisible(true);
      this._title.innerHTML = message;
      this._div.style.left = position.x + 10 + "px";
      this._div.style.top = (position.y - this._div.clientHeight / 2) + "px";
      this.message = message;
    }
  };

  return new tooltip(frameDiv);
}


export default {
  messagePrompt,
  notifyPrompt,
  loadingPrompt,
  messageBoxConfirm,
  createTooltip
}
